body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Aclonica', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #303030;
  color: #FFFFFF;
  font-weight: normal;
}

.walletP {
  margin: 0;
}

#timerText {
  margin: 10px 0 0 0;
  color: #8d8d8d;
  font-weight: bold;
  user-select: none;
}

#timerText:hover {
  margin: 10px 0 0 0;
  color: #84ff3d;
  font-weight:bold;
  user-select: none;
}

#timerTextHidden{
  display: none;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



#connectButtonStyle{
  background-color: #910b59;
  font-weight: normal;
  font-family: 'Aclonica';
  /* width: 100px;
  height: 100px; */
  /* border-radius:50%; */
 
  -webkit-animation: glowing 1s ease-in-out infinite alternate;
  -moz-animation: glowing 1s ease-in-out infinite alternate;
  animation: glowing1 1s ease-in-out infinite alternate;
  

}

@keyframes glowing1 {
  from {
    box-shadow: 0 0 0px #fff, 0 0 0px rgb(216, 159, 255), 0 0 0px rgb(255, 0, 234);
  }
  to {
    box-shadow: 0 0 10px rgb(255, 0, 149), 0 0 20px #ffffff, 0 0 30px #ff00b3;
  }
}

