#mintButtonStyle{
  background-color: #ff8800;
  font-weight: bold;

  /* width: 100px;
  height: 100px; */
  /* border-radius:50%; */
 
  -webkit-animation: glowing 1s ease-in-out infinite alternate;
  -moz-animation: glowing 1s ease-in-out infinite alternate;
  animation: glowing 1s ease-in-out infinite alternate;
  

}

@keyframes glowing {
  from {
    box-shadow: 0 0 0px #fff, 0 0 0px rgb(247, 222, 0), 0 0 0px rgb(255, 187, 0);
  }
  to {
    box-shadow: 0 0 10px rgb(251, 255, 0), 0 0 20px #ff7300, 0 0 30px #fffb00;
  }
}

#mintButtonStyle:hover {     
  background-color:rgba(255, 251, 0, 0.87);  
  color: black;  
}
#minntButtonStyle:active {     
  background-color:rgb(145, 255, 0);    
}


.footer{
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-direction: row;
  margin-top: auto;
}

.roundedFooterSocial{
  background-color: rgba(0, 0, 0, 0.411);
  width: 40px;
  height: 40px;
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 7px 5px 7px;
  
}

.footerButtonDiscord:hover{
color: blueviolet;
}

.footerButtonTwitter:hover{
  color: rgb(0, 119, 255);
}

.footerButtonTelegram:hover{
    color: rgb(0, 204, 255);
}


.mainPicture{
  display: flex;
  margin: 20px 0 25px 0;
  user-select: none;
  pointer-events: none;
  border-radius: 10px;
  box-shadow: 
  0 13px 27px -5px rgba(66, 60, 0, 0.623), 
  0 8px 16px -8px rgba(44, 40, 0, 0.623), 
  0 -6px 16px -6px rgba(70, 63, 0, 0.623);
}

.supplyText{
  margin: 0;
  color: rgb(255, 252, 55);

  font-size: 1.2rem;
}

.supplyText:hover{
  margin: 0;
  color: rgb(0, 248, 33);
}

#myVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  z-index: -1;
}